<template>
  <div class="g-orderdetails">
    <div class="m-address">
      <div class="s-msg">
        <span class="name">{{orderList.order.contact}}</span>
        <span class="phone">{{orderList.order.phone}}</span>
      </div>
      <div class="s-addressDetails beyond">
        {{orderList.order.province}}{{orderList.order.city}}{{orderList.order.area}}{{orderList.order.address}}
      </div>
      <i class="s-rightArrow"></i>
    </div>
    <div class="m-gooddetails">
      <div class="s-title">商品清单</div>
      <div class="s-goods">
        <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+orderList.orderPro.proImg" alt="">
        <div>
          <span>{{orderList.orderPro.proName}}</span>
          <b>
            <i>x{{orderList.orderPro.proNumber}}</i> ￥{{orderList.orderPro.proPrice}}
          </b>
        </div>
      </div>
    </div>
    <div class="m-cost">
      <div class="s-title">费用明细</div>
      <div class="s-box after">
        <span class="left">商品金额</span>
        <span class="right">{{orderList.order.orderProPrice}}{{orderList.orderPro.sortId==1||orderList.orderPro.sortId==2?'积分':'元宝'}}</span>
      </div>
      <div class="s-box after">
        <span class="left">运费</span>
        <span class="right">{{orderList.order.freight||'无'}}{{orderList.orderPro.sortId==1||orderList.orderPro.sortId==2?'积分':'元宝'}}</span>
      </div>
      <div class="s-box after">
        <span class="left">订单金额</span>
        <span class="right">{{orderList.order.orderMoney||'无'}}{{orderList.orderPro.sortId==1||orderList.orderPro.sortId==2?'积分':'元宝'}}</span>
      </div>
    </div>
    <div class="m-msg">
      <div class="s-title">订单信息</div>
      <div class="s-box after">
        <span class="left">订单编号</span>
        <span class="right">{{orderList.order.orderNumber}}</span>
      </div>
      <div class="s-box after" v-if="orderList.order.trackingNumber">
        <span class="left">物流单号</span>
        <span class="right">{{orderList.order.trackingNumber}}</span>
      </div>
      <div class="s-box after">
        <span class="left">下单时间</span>
        <span class="right">{{orderList.order.createTimeStr}}</span>
      </div>
      <div class="s-box after">
        <span class="left">订单状态</span>
        <span class="right">{{orderList.order.state==1?'待支付':orderList.order.state==2?'待发货':orderList.order.state==3?'待确认收货':'已完成'}}</span>
      </div>
    </div>
    <div class="m-btn">
      <div class="s-btned" @click.stop="confirming(orderList.order.id)" v-if="orderList.order.state==3">确认收货</div>
      <div class="s-btned" @click.stop="paying(orderList.order.id)"  v-if="orderList.order.state==1">去支付</div>
      <div class="s-btned" @click.stop="finding(orderList.order.username)">联系客服</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "orderdetials",
    data(){
      return{
        orderList:{
          order:{
            contact:''
          },
          orderPro:{
            proImg:''
          }
        }
      }
    },
    created(){
      this.orderD();//订单详情
    },
    methods:{
      orderD(){
        this.$post(this.$bee+'/api/order/orderDetail',{id:this.$route.query.orderid}).then((data)=>{
          if(data.code==200){
            this.orderList=data.data;
            this.orderList.orderPro.proImg=this.orderList.orderPro.proImg.split(',')[0]
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      paying(v){/*立即支付*/
        this.$createDialog({
          type: 'confirm',
          icon: 'cubeic-alert',
          title: '是否确认支付',
          confirmBtn: {
            text: '确定支付',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            this.$post(this.$bee+'/api/order/payOrder',{orderId:v}).then((data)=> {
              if (data.code == 200) {
                this.orderD();//订单详情
              } else {
                this.$createToast({
                  txt: data.message,
                  type: 'txt'
                }).show();
              }
            })
          },
          onCancel: () => {
            console.log('取消')
          }
        }).show()
      },
      finding(v){/*联系客服*/
        this.$createDialog({
          type: 'alert',
          title: '会员号：'+v
        }).show()
      },
      confirming(v){/*确认收货*/
        this.$createDialog({
          type: 'confirm',
          icon: 'cubeic-alert',
          title: '是否确认收货',
          confirmBtn: {
            text: '确定收货',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            console.log(v)
            this.$post(this.$bee+'/api/order/receiptOrder',
              {username:sessionStorage.getItem('userid'),orderId:v}
            ).then((data)=> {
              if (data.code == 200) {
                this.orderList.order.state=5;
              } else {
                this.$createToast({
                  txt: data.message,
                  type: 'txt'
                }).show();
              }
            })
          },
          onCancel: () => {
            console.log('取消')
          }
        }).show()
      }
    }
  }
</script>

<style scoped lang="stylus">
.g-orderdetails
  padding-bottom 60px
  .m-address
    box-sizing border-box
    font-size 13.5px
    color: #1a1a1a;
    text-align left
    padding 20px 15px 15px
    position relative
    border-bottom 5px solid #e6e6e6
    .s-msg
      .phone
        float right
    .s-addressDetails
      width 100%
      margin-top 20px
  .m-gooddetails
    padding 10px
    padding-top 20px
    .s-title
      text-align left
      padding-left 5px
      color: #1a1a1a;
      font-size 16px
    .s-goods
      display flex
      flex-wrap flex
      background-color: #f2f2f2;
      padding 10px
      margin-top 15px
      padding-left 15px
      img
        width 70px
        height 70px
      div
        text-align left
        padding-left 10px
        span
          display block
          font-size: 14px;
          text-align left
          height auto
          line-height 1.3
          color: #1a1a1a;
          overflow : hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        b
          font-weight normal
          font-size: 16px;
          color: #1a1a1a;
          margin-top 20px
          display block
          i
            font-style:normal
            color: #808080;
  .m-cost,.m-msg
    padding 10px
    padding-top 20px
    .s-title
      text-align left
      margin-bottom 5px
      color: #1a1a1a;
      font-size 16px
    .s-box
      border-bottom 1px solid #ccc
      height 35px
      line-height 35px
      span
        font-size: 14px;
        color: #666666;
        &.left
          float left
        &.right
          color: #1a1a1a;
          float right
  .m-btn
    border-top 1px solid #e6e6e6
    text-align right
    padding 0 13px
    height 40px
    position fixed
    bottom 0
    width 100%
    box-sizing border-box
    background #fff
    .s-btned
      width 57.5px
      height 25px
      line-height 25px
      margin-top 7.5px
      color #fff
      font-size 12px
      background #e65f16
      display inline-block
      text-align center
      margin-left 7px
      vertical-align: middle
</style>
